@import url('https://fonts.googleapis.com/css2?family=Codystar&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');


.button-45 {
    align-items: center;
    justify-content: center;
    background-color: #FFE7E7;
    background-position: 0 0;
    border: 1px solid #FEE0E0;
    border-radius: 11px;
    width: 40%;
    box-sizing: border-box;
    color: #D33A2C;
    cursor: pointer;
    display: flex;
    font-size: 1rem;
    font-weight: 700;
    line-height: 33.4929px;
    list-style: outside url(https://www.smashingmagazine.com/images/bullet.svg) none;
    padding: 2px 12px;
    text-align: center;
    text-decoration: none;
    text-shadow: none;
    text-underline-offset: 1px;
    transition: border .2s ease-in-out,box-shadow .2s ease-in-out;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    white-space: nowrap;
    word-break: break-word;
  }
  
  .button-45:active,
  .button-45:hover,
  .button-45:focus {
    outline: 0;
  }
  
  
  .button-45:active {
    background-color: #D33A2C;
    box-shadow: rgba(0, 0, 0, 0.12) 0 1px 3px 0 inset;
    color: #FFFFFF;
  }
  
  .button-45:hover {
    background-color: #FFE3E3;
    border-color: #FAA4A4;
  }
  
  .button-45:active:hover,
  .button-45:focus:hover,
  .button-45:focus {
    background-color: #D33A2C;
    box-shadow: rgba(0, 0, 0, 0.12) 0 1px 3px 0 inset;
    color: #FFFFFF;
  }
.background {
    background: linear-gradient(90deg, #303030, #161616);
    min-height: 100vh;
    width: 100%;
    display: flex; 
    flex-direction: column; 
    align-items: center;
    justify-content: center;
}
.title-container {
    display: grid;
    grid-template-columns: 2fr 2fr;
    row-gap: 10px;
    grid-template-rows: 2fr 1fr;
    align-items: center;
    justify-items: center;
    width: 30%; 
    margin-top: 3rem;
}


.titleexp {
    font-family: Codystar, sans-serif;
    font-size: 50px;
    color: white;
    text-align: center;
    grid-row: 1;
    grid-column: span 2;
}
.subtext {
    padding-top: 3rem;
    font-family: Ubuntu, sans-serif;
    font-size: 18px;
    text-align: center;
    color: white;
    width: 60%;
    line-height: 2rem;
    margin: auto;
}


.projects{
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    margin-top: 3rem;
    row-gap: 50px;
    margin-bottom: 12rem;
}


.project{
    width: 40%;
    justify-content: center;
    align-items: center;
    margin: auto;
    display: flex;
    flex-wrap: nowrap;
    transform: translateX(30px);
}

.project img{
    max-width: 100%;
    max-height: 900px;
    height: auto;
    grid-column: 1;
    grid-row: 1;
    border: 2mm ridge red;
    margin-right: 10px;
}

.project p {
    font-family: Ubuntu, sans-serif;
    color: white;
    writing-mode: vertical-lr;
    text-orientation: upright;
    height: 50%; 
    font-size: larger;
    text-align: center;
    letter-spacing: 1vw;
    
}

.projectwindow {
    position: absolute;
    transform: translateX(-50%);
    background-color: rgba(0, 0, 0, 0.8); 
    color: #fff;
    padding: 10px;
    transform: translateX(-200%);
    max-width: 200px;
    font-size: 14px;
    text-align: center;
    border-radius: 5px;
    opacity: 0;
    transition: opacity 0.3s ease;
}

.flavor-text {
    white-space: wrap; 

}

.project:hover .projectwindow {
    opacity: 1; 
    pointer-events: auto;
}
